<template>

  <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <Discount />
      <img :src="원룸들[누른거].image" class="room-img" />
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <input v-model.number="month" />
      <p>{{month}}개월 선택함 : {{ month * 원룸들[누른거].price }}원</p>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template> 

<script>
import Discount from './Discount.vue';

export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'Modal',
    data(){
      return{
        month : 1,
      }
    },
    components :{
      Discount : Discount,
    },
    watch : { // 해당 함수가 변경 될 때마다 데이터 감시 
      month(a){ // 변경후, 변경전

        if(a > 13){
          alert('13이상 ㄴㄴ');
          this.month = 1
        }
      },
    },
    props : {
      원룸들 : Array,
      누른거 : Number,
      모달창열렸니 : Boolean,
    }
    
}
</script>

