    }
  },
  methods:{
    increase(i){
      this.신고수[i]++;
    },
    priceSort(){
      this.원룸들.sort(function(a,b){
        return a.price - b.price
      });
    },
    sortBack(){
      this.원룸들 = [...this.원룸들오리지널];
    }

  },
  components: {
    Discount : Discount,
    Modal : Modal,
    Card : Card,
  }
}
</script>





